import { Component, OnInit } from '@angular/core';
import { SiteService } from './site.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ActivatedRoute, UrlSegment, UrlSegmentGroup, PRIMARY_OUTLET } from '@angular/router';

@Component({
  selector: 'app-site',
  templateUrl: './site.component.html',
  styleUrls: ['./site.component.scss']
})
export class SiteComponent implements OnInit {
  myGroup: FormGroup;
  sites: any;
hazards:any;
tests:any;
firstName:any;
    searchtext: string;
  constructor(private siteService: SiteService,private route: ActivatedRoute)
  {
   // debugger;
    if(route.snapshot.url[0])
    {
      this.searchtext=route.snapshot.url[0].path;
      this.sites = this.siteService.sites;

      this.sites = this.sites.filter(element=> element.SiteId==this.searchtext);

      if(this.sites.length==0)
      {
         this.sites = this.siteService.sites.filter(element=> element.Address.toString().toLowerCase().indexOf(this.searchtext.toString().toLowerCase())>0);
      }
      
      if(this.sites.length>0)
      {
       this.hazards = this.siteService.hazards.filter(element=>element.SiteId==this.sites[0].SiteId);
       this.tests = this.siteService.tests.filter(element=>element.SiteId==this.sites[0].SiteId);
      }
    }
    
  }
  form = new FormGroup({
    sitetype:new FormControl(),
    siteuse:new FormControl(),
    company:new FormControl(),
    address:new FormControl(),
    address2:new FormControl(),
    city:new FormControl(),
    state:new FormControl(),
    country:new FormControl(),
    postalCode:new FormControl(),
  });
 
  get first(): any { return this.form.get('first'); }
 
  onSubmit(): void {
    console.log(this.form.value);  // {first: 'Nancy', last: 'Drew'}
  }
 
  setValue() { this.form.setValue({first: 'Carson', last: 'Drew'}); }
  ngOnInit() {
  }

}
